
const style = require("./css/NewsForm.css");
const React = require("react");


export class NewsForm extends React.Component{
    render(){
        return <form className="updateNewsForm" action="./" method="post">
                <h4></h4><h4 style={{fontStyle:"Times"}}><center>UPDATE NEWS</center></h4>
                <label className="updateNewsForm-item" >:</label>
                <input type="text" name="title" className="uploadNewsForm-item inputBox"/>

                <label className="updateNewsForm-item">:</label>
                <input type="text" name="source" className="uploadNewsForm-item inputBox"/>


                <label className="updateNewsForm-item">:</label>
                <textarea rows="13" cols="50" name="content" className="inputBox"></textarea>

                <label>:</label><input type="submit" value="Submit"/>
            </form>;
    }

}


